@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@500&display=swap);
body{
    background-color: rgb(255, 255, 255);
    font-family: 'Noto Sans KR', sans-serif;
    margin: 0;
    padding:0;
}
*{
    outline: none;
    -webkit-appearance: none;
}
.blue-button{
    padding: 10px 20px;
    background-color: #00c1fc;
    border: none;
    border-radius: 10px;
    color: white;
}
.Container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    min-height: 80vh;
    margin-bottom: 50px;
}
.centerContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
#error{
    color: #f57272;
    font-size: small;
    margin-bottom: 50px;
}
/*Main*/
.guestContainer{
    width: 100%;
    padding-top: 50px;
}
.search{
    width: 100%;
}
span{
    font-size: medium;
    color: #383838f8;
}
.guestContainer input[type=text], .hostContainer input{
    padding: 10px 20px;
    background-color: #fcfcfcd0;
    border-radius: 20px;
    border: solid #00c1fc;
    margin: 10px;
    font-size: small;
    width: 100%;
}
.guestContainer input[type=text]{
    box-shadow: 2px 2px 2px 2px #00c1fc41;
    margin-top: -30px;
    width: 300px;
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
}
.search span{
    color: white;
    font-size: large;
}
.hostContainer, .storeContainer{
    background-color: #383838f8;
    width: 100%;
    padding: 50px 0px;
    color: white;
    height: 300px;
}
.hostContainer{
    padding: 180px 0px;
    background-color: white;
}
.hostContainer #toggle{
    margin-right: -200px;
    border: thin solid rgb(209, 209, 209);
    padding: 5px 10px;
    border-radius: 20px;
    font-size: small;
}
.hostContainer input{
    margin: 5px;
    width: 130%;
}
.hostContainer form{
    margin: 50px 0px;
    margin-top: 0;
}
.hostContainer input[type=submit]{
    background: linear-gradient( #00c1fc,  #00c1fc);
    width: 150%;
    color: white;
    border: none;
}
#changeSpan{
    position: absolute;
    top:0;
    right: 0;
    margin: 10px;
    color: #707070;
    z-index: 999;
    border: thin solid rgb(209, 209, 209);
    padding: 5px 10px;
    border-radius: 20px;
    font-size: small;
}
.guest-search-ment{
    font-size: 80px;
    padding: 50px 40px;
    margin-top: 50px;
    width: 190px;
}
.guest-search-ment *{
    margin: 10px;
    color: #9e9e9e;
}
.box-container{
    box-shadow: 2px 2px 1px 1px rgb(237, 237, 237);
    border: thin solid #00c1fc;
    border-radius: 10px;
    width: 280px;
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    margin: 20px;
}
.storeBox #go{
    position: absolute;
    -webkit-transform: translate(260px,0px);
            transform: translate(260px,0px);
}
.storeBox #subName{
    padding-left: 10px;
    font-size: x-small;
}
/*guestStore*/
#map{
    margin-top: 30px;
}
.storeContainer{
    height: 100%;
}
.storeContainer span{
    color: white;
}
.waitInfo{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: white;
    padding: 10px 50px;
    padding-top: 30px;
    width: 200px;
    border-radius: 20px;
    margin: 30px 0px;
    border:  solid #00c1fc;
}
.waitInfo span{
    font-size: x-large;
    font-weight: 600;
    margin: 5px;
    color: rgb(60, 60, 60);
}
.waitInfo-span-wrap{
    display: -webkit-flex;
    display: flex;
}
.waitInfo #number{
    color: #00c1fc;
    font-size: 50px;
    margin-left: 50px;
    -webkit-transform: translate(0, -20px);
            transform: translate(0, -20px);
}
.waitInfo #small{
    font-size: medium;
    -webkit-transform: translate(0, 10px);
            transform: translate(0, 10px);
}
#storeName{
    font-size: large;
    font-weight: 600;
}
#storeSubName{
    font-size: small;
    margin: 5px;
}
#reserve{
    font-size: medium;
    margin: 10px;
}
.storeinfo{
    width: 90%;
    font-size: x-small;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
    padding-bottom: 50px;
}
.storeinfo hr{
    width: 100%;
    border: none;
    margin-top: 50px;
    border-top: thin solid #00c1fc;
}
.storeinfo #title{
    margin-right: 190px;
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: small;
    color: rgb(79, 79, 79);
}
#call-link{
    text-decoration: none;
    font-size: xx-small;
    margin-left: 20px;
}
#call-link:link {
    color : white;
  }
#call-link:visited {
    color : white;
  }
#call-link:hover {
   color : white;
  }
#call-link:active {
    color : white
  }
/*hostStore*/
#store-del{
    padding: 50px;
    color: rgb(184, 67, 67);
}
.waitInfo-wrap{
    margin: 50px 0px;
}
.waitInfo-wrap .toggle-btn{
    color: #00c1fc;
}
.waitInfo-wrap form{
    margin-bottom: 30px;
}
.waitInfo-wrap input[type=text]{
    width: 50px;
    margin-left: 30px;
    font-size: xx-large;
    text-align: end;
    border-radius: 10px;
    border: thin solid rgb(212, 212, 212);
    padding: 0;
}
.waitInfo-wrap input[type=submit]{
    font-size: medium;
    border: none;
    margin: 10px;
}
/*addSotre*/
.aboutStore{
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 400px;
}
.aboutStore-form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
    padding: 50px 30px;
    width: 100%;
}
.aboutStore-form *{
    margin: 10px 0px;
}
.aboutStore-form input[type=text],.aboutStore-form input[type=tel], #textArea{
    padding: 10px;
    border: thin solid #b8b8b8f8;
    border-radius: 0;
    font-size: medium;
}
.aboutStore-form input[name=storeCall]{
    width: 100px;
    
}
.title{
    background-color: #383838f8;
    display: block;
    width: 100%;
    text-align: center;
    padding: 30px 0px;
    color: white;
}

#textArea{
    overflow:hidden;
    resize: none;
    font-size: medium;
    line-height: 1.5;
}
.postCode{
    width: 120%;
}
.aboutStore-form-time input{
    width: 30px;
}
#search-box{
    background-color: whitesmoke;
    width: 100px;
    text-align: center;
    padding: 10px 0px;
}
#require-mark{
    color: #b55353;
}
#address{
    padding: 10px;
    border: thin solid #b8b8b8f8;
    border-radius: 0;
    font-family:sans-serif;
    font-size: small;
}
#attachmentDel{
    color: #b55353;
}
.aboutStore-form input[type=submit]{
    padding: 10px 20px;
    background-color: #00c1fc;
    border: none;
    margin: 10px;
    border-radius: 20px;
    color: white;
    font-size: medium;
}
/*hostHome*/
.hostHome span{
    color: white;
}
.hostHome{
    margin-bottom: 30px;
}
#side-menu, #side-menu2{
    position: absolute;
    right: 0;
    margin: 0px 20px;
    margin-top: -5px;
    font-size: small;
    color: #b55353;
    border: thin solid #b55353;
    padding: 5px 10px;
    border-radius: 20px;
}
#add-btn{
    margin: 10px;
}
/*waiting*/
.waitingBox{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 300px;
}
.enter-btn{
    position: absolute;
    -webkit-transform: translate(250px);
            transform: translate(250px);
    background-color: #00c1fc;
    padding: 5px 10px;
    border-radius: 20px;
    color: white;
}
#side-menu2{
    position: absolute;
    top:0;
    right: 0;
    margin: 15px;
    border: thin solid #00c1fc;
    color: #00c1fc;
}
#time{
    position: absolute;
    -webkit-transform: translate(255px, 50px);
            transform: translate(255px, 50px);
    color: #707070;
    font-size:small;
}
/*reserve*/
.reserve-wrap{
    width: 70%;
    border: solid #00c1fc;
    background-color: rgba(255, 255, 255, 0.068);
    border-radius: 20px;
    padding: 30px 10px;
    color: #707070;
}
.reserve-wrap .ment{
    font-size: small;
    text-align: center;
    width: 80%;
    color: #ff9191;
}
.reserve-input{
    display: -webkit-flex;
    display: flex;
    margin: 5px;
}
.reserve-input span{
    width: 60px;
}
.reserve-input input{
    border:none;
    padding: 5px 10px;
    border-radius: 0;
    width: 50px;
    background-color: rgb(221, 221, 221);
    border-radius: 10px;
    font-size: medium;
}
.phone input{
    width: 100px;
}
.reserve-wrap input[type=submit]{
    background-color: #00c1fc;
    padding:  5px 20px;
    border: none;
    border-radius: 20px;
    color: white;
    margin: 10px;
    font-size: medium;
}
#side-menu3{
    position: absolute;
    border-radius: 20px;
    padding: 5px 10px;
    top:0;
    left: 0;
    margin: 15px;
    font-size: small;
    border: thin solid white;
    color: white;
}
.waiting-ment{
    padding: 20px;
    margin-top: 50px;
    color: #00c1fc;
    border: thin solid rgb(206, 206, 206);
    border-radius: 20px;
}
#waiting-count{
    background-color: #00c1fc;
    padding:  5px 10px;
    border: none;
    border-radius: 20px;
    color: white;
    margin: 10px;
}
/*footer*/
footer{
    text-align: center;
    width: 100%;
    color: #707070;
    border-top: thin solid #707070;
    padding:10px 0;
    font-size: xx-small;
}
#loading{
    display: block;
    text-align: center;
    -webkit-transform: translate(0, 50%);
            transform: translate(0, 50%);
    height: 80vh;
    color: #00c1fc;
}
